import {
  AfterViewInit,
  ChangeDetectorRef,
  Directive,
  HostBinding,
  Input,
} from '@angular/core';

const BUTTON_CLASSES = {
  primary: 'bc-button-primary',
  secondary: 'bc-button-secondary',
  terciary: 'bc-button-terciary',
  tertiary: 'bc-button-tertiary',
  background: 'bc-button-background',
  ghost: 'bc-button-ghost',
  default: 'bc-button-primary',
};
const BUTTON_SIZES = {
  small: 'bc-button-small',
  puffy: 'bc-button-puffy',
  default: 'bc-button',
};
const BUTTON_WIDTH_CLASSES = {
  fill: 'bc-button-fill',
  hug: 'bc-button-hug',
  default: 'bc-button-fill',
};

/**
 * Importa la directiva de botones con los diseños de Bancolombia S.A.
 *
 * @export
 * @class BcButtonDirective
 */
@Directive({
  selector: '[bc-button]',
})
export class BcButtonDirective implements AfterViewInit {
  constructor(private changeDetectorRef: ChangeDetectorRef) {}

  /**
   * Parámetro para denotar el tipo de botón.
   *
   * @type {('primary' | 'secondary' | 'terciary' | 'tertiary' | 'background' | 'ghost')}
   * @memberof BcButtonDirective
   */
  @Input() typeButton:
    | 'primary'
    | 'secondary'
    | 'terciary'
    | 'tertiary'
    | 'background'
    | 'ghost';

  /**
   * Parámetro para denotar el tamaño de botón.
   *
   * @type {('default' | 'small' | 'puffy')}
   * @memberof BcButtonDirective
   */
  @Input() sizeButton: 'default' | 'small' | 'puffy';

  /**
   * Parámetro para denotar la configuración de ancho del botón.
   *
   * @type {('fill' | 'hug')}
   * @memberof BcButtonDirective
   */
  @Input() width: 'fill' | 'hug';

  /**
   * Parámetro para denotar la clase de botón.
   *
   * @type {string}
   * @memberof BcButtonDirective
   */
  @Input() class = '';

  @HostBinding('class') classes = [
    this.class,
    this.setStyles(),
    this.setSize(),
    this.setWidth(),
  ].join(' ');

  /**
   * Configura los estilos del botón.
   *
   * @returns {string}
   * @memberof BcButtonDirective
   */
  setStyles(): string {
    return BUTTON_CLASSES[this.typeButton] || BUTTON_CLASSES.default;
  }

  /**
   * Configura el tamaño del botón.
   *
   * @returns {string}
   * @memberof BcButtonDirective
   */
  setSize(): string {
    return BUTTON_SIZES[this.sizeButton] || BUTTON_SIZES.default;
  }

  /**
   * Configura el ancho del botón.
   *
   * @returns {string}
   * @memberof BcButtonDirective
   */
  setWidth() {
    return BUTTON_WIDTH_CLASSES[this.width] || BUTTON_WIDTH_CLASSES.default;
  }

  ngAfterViewInit() {
    this.classes = [
      this.class,
      this.setStyles(),
      this.setSize(),
      this.setWidth(),
    ].join(' ');
    this.changeDetectorRef.detectChanges();
  }
}
